@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Lato:wght@100;300;400;700;900&family=Montserrat:wght@100;200;300;400;500;600;700&family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap');

.login_container {
	width: 100%;
	min-height: 100vh;
	background-color: #f5f5f5;
	display: flex;
	align-items: center;
	justify-content: center;
}

.login_form_container {
	width: 100%;
	height: 100vh;
	display: flex;
	border-radius: 0px;
	box-shadow: 0px 3px 3px -2px rgb(0 0 0 / 20%),
	0px 3px 4px 0px rgba(173, 42, 42, 0.14), 0px 1px 8px 0px rgb(0 0 0 / 12%);
}

.left {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: white;
	border-top-left-radius: 10px;
	border-bottom-left-radius: 10px;
}
.left h1{
	color: #ED8F00;
}

.form_container {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.form_container h1 {
	font-size: 40px;
	margin-top: 0;
}

.input {
	outline: none;
	border: none;
	width: 370px;
	padding: 15px;
	border-radius: 10px;
	background-color: #edf5f3;
	margin: 5px 0;
	font-size: 14px;
}

.error_msg {
	width: 370px;
	padding: 15px;
	margin: 5px 0;
	font-size: 14px;
	background-color: #f34646;
	color: white;
	border-radius: 5px;
	text-align: center;
}

.right {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: #B73E3E;
	border-top-right-radius: 0px;
	border-bottom-right-radius: 0px;
}

.right h1 {
	color: white;
	font-size: 2.5em;
	align-self: center;
}

.white_btn,
.green_btn {
	border: none;
	outline: none;
	padding: 12px 0;
	background-color: rgba(0,0,0,0);
	color: white;
	border-radius: 20px;
	width: 180px;
	font-weight: bold;
	font-size: 14px;
	cursor: pointer;
	border: solid white;
}

.green_btn {
	background-color: rgba(0,0,0,0);
	color: #ED8F00;
	border: solid #ED8F00;
	margin: 10px;
	transition: 0.5s;
}

.green_btn:hover{
	background-color: #ED8F00;
	color: white;
}

button {

	align-items: center;
	justify-content: center;

	margin: 2px;
	cursor: pointer;

}

button img {
	width: 24px;
	height: 24px;
	margin-right: 1px;
}

.facebook {
	background-color: #247df1;
	border: none;
	margin-right: 10px; /* Add some space between the buttons */
	border-radius: 5px;
	margin-top:33px;
	width: 75px;
	height: 45px;
	transition: 0.3s;
}

.facebook:hover{
	filter: brightness(90%);
}

.Google {
	border-color: #B73E3E;
	background-color: transparent;
	border-width: 1px;
	border-radius: 5px;
	width: 75px;
	height: 45px;
	transition: 0.3s;
}

.Google:hover{
	background-color: rgb(236, 236, 236);
}

.tel:hover{
	background-color: rgb(236, 236, 236);
}
.tele{
	background-color: gray;
	border: none;
	margin-right: 20px; /* Add some space between the buttons */
	border-radius: 5px;
	margin-top:33px;
	width: 75px;
	height: 45px;
}

.button-container {
	display: flex; /* Use flexbox to make the buttons appear in one line */
	align-items: center; /* Center the buttons vertically */
	justify-content: center; /* Center the buttons horizontally */
	margin-top: 40px; /* Add some top margin to the container */
}
.button_container h2 {
	margin: 0;
	font-size: 14px;
	color: gray;
	text-align: center;
	margin-top: 15px;
}
.tel{
	background-color:rgba(0,0,0,0) ;
	border: solid black;
	border-width: 1px;
	margin-left: 10px; /* Add some space between the buttons */
	border-radius: 5px;
	margin-top:33px;
	width: 75px;
	height: 45px;
	transition: 0.3s;
}

.img2{
	position: absolute;
	bottom: 175px;
	height: 70vh;
	width: 90%;
	max-width: 300px;
	object-fit: contain;
	justify-content: center;
	align-items: center;
}

.newhere{
	margin: 0;
	position: absolute;
	top: 54vh;
	left: 68%;
}

.white_btn{
	margin: 0;
	position: absolute;
	top: 85%;
	left: 70%;
	transition: 0.5s;
}

.white_btn:hover{
	background-color: white;
	color: #B73E3E;
}



.right h1{
	margin-top: 170px;
}





